const ADD_MESSAGE = 'ADD-MESSAGE';

let initialState = {
    dialogs: [
        { id: '1', name: ' ', message: ',  ?' },
        { id: '2', name: ' ', message: ',  ?' },
        { id: '3', name: ' ', message: ',  ?' },
        { id: '4', name: ' ', message: ',  ?' },
        { id: '5', name: ' ', message: ',  ?' },
        { id: '6', name: ' ', message: ',  ?' }
    ],
    messages: [
        { id: '1', name: ' ', text: ',  ?', time: '9:30' },
        { id: '2', name: ' ', text: ',  ?', time: '9:30' },
        { id: '3', name: ' ', text: ',  ?', time: '9:30' },
        { id: '4', name: ' ', text: ',  ?', time: '9:30' },
        { id: '5', name: ' ', text: ',  ?', time: '9:30' },
        { id: '6', name: ' ', text: ',  ?', time: '9:30' },
        { id: '7', name: ' ', text: ',  ?', time: '9:30' },
        { id: '8', name: ' ', text: ',  ?', time: '9:30' },
        { id: '9', name: ' ', text: ',  ?', time: '9:30' },
        { id: '10', name: ' ', text: ',  ?', time: '9:30' },
        { id: '11', name: ' ', text: ',  ?', time: '9:30' },
        { id: '12', name: ' ', text: ',  ?', time: '9:30' }
    ],
};

const dialogsReducer = (state = initialState, action) => {
    switch(action.type){
        case ADD_MESSAGE:{
            let newMessage = { id: '1', name: ' ', text: action.message, time: '9:45' };

            let stateCopy = {...state};
            stateCopy.messages = [...state.messages];
            stateCopy.messages.push(newMessage);
            return stateCopy;
        }
        default:{
            return state;      
        }
    }
}

export const addMessageActionCreator = (message) => {
    return {type: ADD_MESSAGE, message: message};
};

export default dialogsReducer;