export const composeValidators = (...validators) => value => validators.reduce((error, validator) => error || validator(value), undefined)

export let required = (value) => {
    if (value) {
        return undefined;
    }
    else {
        return '  !';
    }
}

export const maxLengthCreator = size => value => {
    if (value.length > size) {
        return '  !'
    }
    else {
        return undefined;
    }
}  